
html {
  --blue: #0072B9;
  --yellow: #ffc600;
  font-size: 62.5%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 2rem;
  line-height: 1.5;
  background: url("./bgface.jpg");
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

h1{
  margin:10px 0 -30px;
  color:white;
  text-align: center;
}

.bigBox{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.controls{
  margin-top:100px;
}

label{
  color :var(--blue);
}


.wrap {
  position: relative;
  min-height: 70vh;
  display: grid;
  justify-content: center;
  align-items: center;
}

.wrap>* {
  grid-column: 1;
  grid-row: 1;
}


.face {
  position: absolute;
}